.tablemain, th, td {
  margin: 0 auto;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 300px;
  text-align: center;
  border: 3px solid rgb(243, 245, 247);;
  border-collapse: collapse;
  padding: 5px;
  font-family: 'Times New Roman', Times, serif;
  font-weight: 800;
  color: rgb(38, 40, 43);  
}

.tablemain {
    -webkit-box-shadow: -1px 10px 5px 0px rgba(194, 194, 194, 1);
  -moz-box-shadow: -1px 10px 5px 0px rgba(194, 194, 194, 1);
  box-shadow: -1px 10px 5px 0px rgba(194, 194, 194, 1);
}

th{
    background-color: #00BCD5;
    color: #ffffff;

}

tbody tr:nth-child(even) {
    background-color: #ECF1F4;
}

